export const formatCurrency = (value: number) => {
  return new Intl.NumberFormat('pt-BR', {
    style: 'currency',
    currency: 'BRL'
  }).format(Number(value));
};

export const formatCurrencyInput = (value: string) => {
  // Remove tudo que não é número
  const numbers = value.replace(/\D/g, '');

  // Converte para número com 2 casas decimais
  const amount = Number(numbers) / 100;

  // Formata como moeda
  return new Intl.NumberFormat('pt-BR', {
    minimumFractionDigits: 2,
    maximumFractionDigits: 2,
  }).format(amount);
};

export const parseCurrencyInput = (value: string): number => {
  // Remove tudo que não é número
  const numbers = value.replace(/\D/g, '');

  // Converte para número com 2 casas decimais
  return Number(numbers) / 100;
};

export const formatDate = (date: string | Date | null) => {
  try {
    if (!date) return 'Data inválida';
    return new Intl.DateTimeFormat('pt-BR', {
      year: 'numeric',
      month: '2-digit',
      day: '2-digit',
      hour: '2-digit',
      minute: '2-digit',
    }).format(new Date(date));
  } catch (err) {
    return 'Data inválida';
  }
};

// vamos criar uma função que ira receber uma data e retornar a cor de acordo com o vencimento
export const getDueDateColor = (dueDate: Date | string | null) => {
  if (!dueDate) return 'gray';
  const today = new Date();
  const due = new Date(dueDate);
  // zera as horas, minutos, segundos e milissegundos para comparar apenas a data
  today.setHours(0, 0, 0, 0);
  due.setHours(0, 0, 0, 0);

  // se for menor que hoje, retorna vermelho
  if (due < today) return 'red';

  // se for igual a hoje, retorna laranja
  if (due.getTime() === today.getTime()) return 'orange';

  // se for maior que hoje e faltar 3 dias ou menos para o vencimento, retorna amarelo
  const diffTime = due.getTime() - today.getTime();
  const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
  if (diffDays <= 3) return 'yellow';
  return 'green';
}